import { useRouter } from 'next/router';

const MEDIHEAL = 'mediheal';
const productNames = [MEDIHEAL] as const;
export type ProductName = (typeof productNames)[number];

const getProductName = (name: string = MEDIHEAL): ProductName => {
  return productNames.includes(name as ProductName)
    ? (name as ProductName)
    : MEDIHEAL;
};

interface Return {
  name: ProductName;
  readableName: string;
  logoUrl: string;
}

const useProductInfo = (): Return => {
  const router = useRouter();
  const name = getProductName(router.query?.productName as string);
  const readableName = '메디힐';
  const logoUrl = `./images/${name}/logo.svg`;
  return {
    name,
    readableName,
    logoUrl,
  };
};

export default useProductInfo;
