import { css } from '@emotion/react';
import { useCallback, useEffect, useState } from 'react';

import { Typography } from 'src/components/dataDisplay';
import { PlusIcon } from 'src/components/icons';
import { Stack } from 'src/components/layout';
import type { LinkableContent } from 'src/hooks/usePaperItems';

const contentWrapper = (top: number) => css`
  position: absolute;
  top: ${top}px;
`;

const leftStyle = (left: number) => css`
  left: ${left}px;
`;

const rightStyle = (right: number) => css`
  right: ${right}px;
`;

const mint = css`
  color: #5edfdf;
`;

const transparent = css`
  color: transparent;
`;

const noButton = css`
  display: contents;
  color: inherit;
`;

const getIconStyle = (hasColor: boolean) => (hasColor ? mint : transparent);

const BASE_HEIGHT = 712;
const BASE_WIDTH = 375;

interface LinkerProps extends LinkableContent {}

interface LinkerContentProps
  extends Pick<LinkerProps, 'title' | 'description'> {}
const LinkerContent = ({
  title,
  description,
}: LinkerContentProps): JSX.Element => {
  return (
    <Stack>
      <Typography variant={title.variant}>{title.content}</Typography>
      {description && (
        <Typography variant={description.variant}>
          {description.content}
        </Typography>
      )}
    </Stack>
  );
};

const PointingContent = ({
  title,
  description,
  position,
  direction,
  link,
}: LinkerProps): JSX.Element => {
  const [verticalPosition, setVerticalPosition] = useState(0);
  const [horizontalPosition, setHorizontalPosition] = useState(0);

  const onResize = useCallback(() => {
    // height - 비율 따라
    // 높이가 안바뀌었으면 그냥 그대로
    // 높이가 바뀌었으면 비례식에 따라이지만 최대한 곱연산을 줄여 지금 top 값에 늘어날 비율만 더하도록 처리
    const heightDiff = window.innerHeight - BASE_HEIGHT;
    const diffForHeightAlign =
      heightDiff === 0 ? 0 : (heightDiff * position.top) / BASE_HEIGHT;
    const topWithDiff = position.top + diffForHeightAlign;
    setHorizontalPosition(topWithDiff);

    // width - 좌우 좁혀진 너비에 따라 조정
    // 높이가 안바뀌었으면 그냥 그대로
    // 높이가 좌표계가 늘어난 만큼 비율로 곱해서 시작..
    // 너비가 안바뀌었으면 그대로
    // 너비가 바뀌면 바뀐 값만큼
    const rate = heightDiff === 0 ? 1 : window.innerHeight / BASE_HEIGHT;
    const innerWidth =
      window.innerWidth > window.innerHeight
        ? window.innerHeight
        : window.innerWidth; // 에외 처리
    const widthDiff = innerWidth / rate - BASE_WIDTH;
    const diffForWidthAlign = widthDiff === 0 ? 0 : widthDiff / 2;
    if (direction === 'right') {
      const leftWithDiff = position.left + diffForWidthAlign;
      setVerticalPosition(leftWithDiff * rate);
    } else {
      const right = BASE_WIDTH - position.left - 24;
      const rightWithDiff = right + diffForWidthAlign;
      setVerticalPosition(rightWithDiff * rate);
    }
  }, [setVerticalPosition, direction, position.top, position.left]);
  useEffect(() => {
    window.addEventListener('resize', onResize);
    onResize();
    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [onResize]);

  const handleClick = () => {
    if (link) {
      window.location.href = link;
    }
  };
  return (
    <button type="button" onClick={handleClick} css={noButton}>
      <Stack
        direction="row"
        alignItems="center"
        spacing={8}
        css={[
          contentWrapper(horizontalPosition),
          direction === 'right' && leftStyle(verticalPosition),
          direction === 'left' && rightStyle(verticalPosition),
        ]}
      >
        {direction === 'right' && (
          <PlusIcon css={getIconStyle(Boolean(link))} />
        )}
        <LinkerContent title={title} description={description} />
        {direction === 'left' && <PlusIcon css={getIconStyle(Boolean(link))} />}
      </Stack>
    </button>
  );
};

export default PointingContent;
