import type { ImgHTMLAttributes } from 'react';

export interface IconBaseProps extends ImgHTMLAttributes<HTMLImageElement> {}

const IconBase = ({
  alt = 'icon-image',
  src,
  width,
  height,
  ...others
}: IconBaseProps): JSX.Element => {
  return <img alt={alt} src={src} width={width} height={height} {...others} />;
};

export default IconBase;
